import React, { Component } from 'react';
import './Assets/scss/default.css';

import {
  BrowserRouter as Router,
  Route,
  Link,
  NavLink,
  Switch,
  IndexLink
} from 'react-router-dom';



// components

import Header from './components/headercomponent/header';
import Footer from './components/footercomponent/footer';
import Homepage from './components/pages/homePage';
import Aboutpage from './components/pages/aboutPage';
import Skills from './components/pages/skillsPage';
import Myworkpage from './components/pages/myworkPage';



class App extends Component {
  render() {
    return (
      <Router>
      <div className="App">
          <Header />
          <Switch>
            <Route exact path = '/' component={Homepage} />
            <Route exact path = '/aboutPage' component={Aboutpage} />
            <Route exact path = '/skillsPage' component={Skills} />
            <Route exact path = '/myworkPage' component={Myworkpage} />
          </Switch>

          <Footer />


        </div>
      </Router>
    );
  }
}

export default App;
