import React, { Component } from 'react';
import {
  NavLink
} from 'react-router-dom';




class Header extends Component {
  render() {
    return (
      <header id="header">
        <div className="container-fluid">
          <div className="row">


          <div className="col-sm-3">
            <a href="#" className="logo">
              <img src="http://demo3.inheritedarts.com/sound-cloud.png" className="logo"/>
            </a>
          </div>



          <div className="col-sm-9">
            <ul id="menu" className=" nav-effect">
              <li>
                <NavLink exact activeClassName='current'to="/" className="">Home</NavLink>
              </li>
              <li>
                <NavLink exact activeClassName='current'to="/aboutPage" className="">About Me</NavLink>
              </li>
              <li>
                <NavLink exact activeClassName='current'to="/skillsPage" className="">My Skills</NavLink>
              </li>
              <li>
                <NavLink activeClassName='current'to="/myworkPage" className="">My Work</NavLink>
              </li>
              <li>
                <NavLink activeClassName='current'to="/contactPage" className="">Email Me</NavLink>
              </li>
            </ul>
          </div>

        </div>
      </div>
    </header>


















    );
  }
}

export default Header;
