import React, { Component } from 'react';





class Skills extends Component {
  render() {
    return (

      <main id="skills">
        <h1 className="lg-heading">
          My <span className="text-secondary">Skills</span>
        </h1>
        <h2 className="sm-heading">
          Check out some of my skills...
        </h2>
        <div className="container">

          <div className="progress-bar" data-percentage="85%">
            <h4 className="progress-title-holder">
              <span className="progress-title">HTML5</span>


            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="75%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">CSS3</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="60%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">BootStrap</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="50%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">JavaScript</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="50%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">jQuery</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="40%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">Python</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="10%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">React</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="10%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">PHP</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>

          <div className="progress-bar" data-percentage="10%">
            <h4 className="progress-title-holder clearfix">
              <span className="progress-title">MySQL</span>

            </h4>
            <div className="progress-content-outter">
              <div className="progress-content"></div>
            </div>
          </div>
        </div>

      </main>

    );
  }
}

export default Skills;
